import React, { useState } from "react";
import { connect } from "react-redux";
import {
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from "@mui/material";
import Box from "@mui/material/Box";
import { HeaderText } from "./HeaderText";
import { serverinfo } from "./ServerInfo";
import { setAllConference } from "../reduxconfig/actions/LoginAction";
import { Typography } from "@mui/material";
import ButtonGourp from "@mui/material/ButtonGroup";
import Button from "@mui/material/Button";
import AddTaskOutlinedIcon from "@mui/icons-material/AddTaskOutlined";
import CancelOutlinedIcon from "@mui/icons-material/CancelOutlined";
import Modal from "@mui/material/Modal";

const FloorModal = (props) => {
  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    bgcolor: "background.paper",
    border: "2px solid #000",
    boxShadow: 24,
    p: 4,
  };
  const [floor, setFloor] = useState();
  const fn_modalClose = () => {
    setFloor(false);
  };
  const fn_addFloor = () => {
    const data = {
      floor: floor,
    };
    fetch(`${serverinfo}/floor`, {
      method: "post",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    })
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        console.log(data);
        if (data.confNo !== 0) {
          alert("층 등록완료");
          fetch(`${serverinfo}/conferences/all`)
            .then((response) => {
              return response.json();
            })
            .then((data) => {
              props.dispatch(setAllConference(data));
              props.setFloorModal(false);
            });
        } else {
          alert("등록실패 다시 시도하세요!");
        }
      });
  };
  const fn_closeModal = () => {
    props.setFloorModal(false);
  };
  const inputData = (e) => {
    setFloor(parseInt(e.target.value));
  };
  return (
    <Modal
      open={props.floorModal}
      onClose={fn_modalClose}
      aria-labelledby="modal-title"
      aria-describedby="modla-content"
    >
      <Box sx={style}>
        <Typography id="modal-title">
          <HeaderText title="회의실 등록하기" />
        </Typography>
        <Typography id="modal-content">
          <Box>
            <Box display={"flex"}>
              <TextField
                sx={{ marginBottom: "5%" }}
                fullWidth
                label="층 등록"
                variant="outlined"
                color="secondary"
                onChange={inputData}
              />
            </Box>
          </Box>
          <Box sx={{ display: "flex", justifyContent: "right" }}>
            <ButtonGourp>
              <Button
                color="success"
                startIcon={<AddTaskOutlinedIcon />}
                onClick={fn_addFloor}
              >
                회의실등록하기
              </Button>
              <Button
                color="error"
                startIcon={<CancelOutlinedIcon />}
                onClick={fn_closeModal}
              >
                닫기
              </Button>
            </ButtonGourp>
          </Box>
        </Typography>
      </Box>
    </Modal>
  );
};
export default connect((state) => state)(FloorModal);
