import { SET_ALLCONF, SET_CURCONF } from "../actions/LoginAction";

export default (state={},action)=>{
    const {type,payload}=action;
    switch(type){
        case SET_CURCONF : return {...state,curconfNo:payload}
        case SET_ALLCONF : return {...state, conferenceInfo:payload}
        default : return state;
    }
}