import React, { useState } from "react";
import AssignmentIndSharpIcon from "@mui/icons-material/AssignmentIndSharp";
import Box from "@mui/material/Box";
import LogoutTwoToneIcon from "@mui/icons-material/LogoutTwoTone";
import Button from "@mui/material/Button";
import { setLogout } from "../reduxconfig/actions/LoginAction";
import { connect } from "react-redux";
import AdminMenu from "./AdminMenu";

export default connect((state) => state)(function LoginSuccessComponent(props) {
  const loginMember = JSON.parse(sessionStorage.getItem("loginMember"));
  const fn_logout = (e) => {
    sessionStorage.removeItem("loginMember");
    props.dispatch(setLogout());
    props.setCaldendar({});
  };
  return (
    <div style={{ width: "80%", marginRight: "20%" }}>
      <Box display="flex" justifyContent={"space-between"} minWidth={"80%"}>
        <Box display={"flex"}>
          <AssignmentIndSharpIcon fontSize="large" />
          <Button sx={{ fontSize: "20px", fontWeight: "bolder" }}>
            {loginMember["memberId"]}
          </Button>
        </Box>
        <Box>
          <LogoutTwoToneIcon fontSize="large" onClick={fn_logout} />
        </Box>
      </Box>
      {loginMember["memberLevel"] == 1 && (
        <Box>
          <AdminMenu />
        </Box>
      )}
    </div>
  );
});
