import React, { useState, useRef, useEffect } from "react";
import FullCalndar, { CalendarApi, constrainPoint } from "@fullcalendar/react";
import dayGridPlugin from "@fullcalendar/daygrid";
import interactionPlugin from "@fullcalendar/interaction";
import timeGridPlugin from "@fullcalendar/timegrid";
import Box from "@mui/material/Box";

import { connect } from "react-redux";
import EnrollModal from "./EnrollModal";
import ReserveViewModal from "./ReserveViewModal";
import { serverinfo } from "./ServerInfo";
import { setEvent, setCalEvent } from "../reduxconfig/actions/EventAction";

const calendarContainer = function CalendarContainer(props) {
  //캘런더를 사용하기 위한 useRef()함수사용하기
  const calendarRef = useRef();

  const [enrollModalFlag, setEnrollModalFlag] = useState(false);

  const [reserveModalFlag, setReserveFlag] = useState(false);

  const [eventViewData, setEventViewData] = useState({});

  const [events, setEvents] = useState({
    title: "",
    start: "",
    end: "",
    id: "",
  });

  const fn_dataClick = (info) => {
    if (new Date().getDate() != info.date.getDate()) {
      alert("당일만 예약할 수 있습니다.");
    } else {
      if (typeof props.curConf["curconfNo"] != "undefined") {
        setEvents((prev) => ({
          ...prev,
          start: info.date,
          confContentNo: props.curConf["curconfNo"], //회의실번호
        }));
        setEnrollModalFlag(true);
      } else {
        alert("회의실을 먼저 선택하세요!");
      }
    }
  };
  useEffect(() => {
    props.dispatch(setCalEvent(calendarRef.current.getApi()));
  }, []);
  const fn_addEvent = (e) => {
    const reservedConferece = props.curConf.conferenceInfo.map((v) => {
      return v.details.filter((v) => {
        return v["confContentNo"] == props.curConf["curconfNo"];
      });
    });
    const sendData = {
      ...events,
      revervedMember: JSON.parse(sessionStorage.getItem("loginMember")),
      reservedConferece: reservedConferece[0][0],
    };

    //등록한 이벤트 DB에 저장하기
    fetch(`${serverinfo}/event`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(sendData),
    })
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        calendarRef.current.getApi().addEvent({
          ...events,
          extendedProps: {
            reservNo: data["reservationno"],
            className: JSON.parse(sessionStorage.getItem("loginMember"))[
              "className"
            ],
          },
        });
        setEvents({ start: "", title: "", end: "", id: "" });
        setEnrollModalFlag(false);
      });
    //calendarRef.current.eventAdd(events);
  };

  const fn_viewEvents = (e) => {
    const target = { ...e };
    setEventViewData((prev) => {
      return target;
    });
    setReserveFlag(true);
  };

  return (
    <div>
      <Box sx={{ width: "90%" }}>
        <FullCalndar
          plugins={[dayGridPlugin, interactionPlugin, timeGridPlugin]}
          initialView="timeGridWeek"
          dateClick={fn_dataClick}
          headerToolbar={{ left: "title", right: "", center: "" }}
          eventClick={fn_viewEvents}
          locale={"ko"}
          slotMinTime={"09:00"}
          slotMaxTime={"19:00"}
          slotDuration={"01:00"}
          ref={calendarRef}
          slotMinWidth={300}
          eventMinWidth={200}
          allDaySlot={false}
          contentHeight={550}
        />
      </Box>
      <EnrollModal
        eventscontroller={{
          events: events,
          setEvents: setEvents,
          fn_addEvent: fn_addEvent,
        }}
        modalcontroller={{
          enrollModalFlag: enrollModalFlag,
          setEnrollModalFlag: setEnrollModalFlag,
        }}
      />
      <ReserveViewModal
        modalController={{
          reserveModalFlag: reserveModalFlag,
          setReserveFlag: setReserveFlag,
        }}
        eventController={{ eventViewData: eventViewData, events: events }}
      />
    </div>
  );
};
class CalEvent {
  constructor(title, start, end, id) {
    this.title = title;
    this.start = start;
    this.end = end;
    this.id = id;
  }
}
export default connect((state) => state)(calendarContainer);
