export const SET_LOGIN='login/SET_LOGIN';
export const SET_LOGOUT='login/SET_LOGOUT';
export const OPEN_MODAL='modal/OPEN_MODAL';
export const CLOSE_MODAL='modal/CLOSE_MODAL';
export const SET_CURCONF='conf/SET_CURCONF';
export const SET_ALLCONF="conf/SET_ALLCONF";
export const SET_ADMIN='admin/SET_ADMIN';

export const setLogin=()=>({
    type:SET_LOGIN,
    payload:true
});

export const setLogout=()=>({
    type:SET_LOGOUT,
    payload:false
});
export const setOpenModal=()=>({
    type:OPEN_MODAL,
    payload:true
})

export const setCloseModal=()=>({
    type:CLOSE_MODAL,
    payload:false
})

export const setCurConference=(value)=>({
    type:SET_CURCONF,
    payload:value
})
export const setAllConference=(value)=>({
    type:SET_ALLCONF,
    payload:value
})

//관리자 권한
export const setAdminCheck=(value)=>({
    type:SET_ADMIN,
    payload:value
})