import React, { useEffect, useRef, useState } from "react";
import TextField from "@mui/material/TextField";
import AdapterDateFns from "@mui/lab/AdapterDateFns";
import LocalizationProvider from "@mui/lab/LocalizationProvider";
import DatePicker from "@mui/lab/DatePicker";
import TimePicker from "@mui/lab/TimePicker";
import Box from "@mui/material/Box";

export default function ConferenceInput({ event }) {
  const { events, setEvents, sendFlag, setSendFlag } = event;

  useEffect(() => {
    setEvents((prev) => ({
      ...prev,
      id: JSON.parse(sessionStorage.getItem("loginMember"))["memberId"],
    }));
  }, []);
  const style = {
    marginBottom: "5%",
    display: "flext",
    flexDirection: "column",
    justifyContent: "space-between",
  };
  const inputStyle = {
    marginBottom: "5%",
  };

  const fn_dateChange = (newValue, oriValue = "") => {
    const reg = /\d{2}:\d{2} (A|P)M?/i;
    if (newValue != null && events["start"] > newValue) {
      alert("시작일보다 빠른시간으로 설정할 수 없습니다.");
      setSendFlag(true);
    } else if (oriValue.length >= 7 && !reg.test(oriValue)) {
      alert("00:00 am|pm 형식에 맞춰서 작성해주세요!");
      setSendFlag(true);
    } else if (oriValue.length < 7) {
      setSendFlag(true);
    } else {
      setEvents((prev) => ({
        ...prev,
        end: newValue,
      }));
      if (
        typeof event.events.title != "undefined" &&
        event.events.title.trim().length > 2
      ) {
        setSendFlag(false);
      } else {
        setSendFlag(true);
      }
    }
  };
  const fn_temp = () => {};
  return (
    <div style={style}>
      <TextField
        fullWidth
        label="사용내용"
        variant="outlined"
        color="secondary"
        sx={inputStyle}
        onChange={(e) => {
          if (e.target.value.trim().length > 2) {
            setEvents((prev) => ({
              ...prev,
              title: e.target.value,
            }));
            if (typeof event.events["end"] != "undefined") {
              setSendFlag(false);
            } else {
              setSendFlag(true);
            }
          } else {
            setSendFlag(true);
          }
        }}
      />

      <LocalizationProvider
        dateAdapter={AdapterDateFns}
        sx={{ display: "flex" }}
      >
        <DatePicker
          label="예약일"
          inputFormat={"yyyy-MM-dd"}
          mask={"____-__-__"}
          value={events["start"]}
          onChange={fn_temp}
          renderInput={(params) => (
            <TextField
              fullWidth
              {...params}
              sx={inputStyle}
              disabled={true}
              readOnly={true}
            />
          )}
        />
        <Box sx={{ display: "flex" }}>
          <TimePicker
            label="시작시간"
            value={events["start"]}
            onChange={(newValue) => {
              setEvents((prev) => ({
                ...prev,
                start: newValue,
              }));
            }}
            renderInput={(params) => (
              <TextField fullWidth {...params} sx={inputStyle} />
            )}
            disabled={true}
          />
          <TimePicker
            label="종료시간"
            value=""
            onChange={fn_dateChange}
            renderInput={(params) => {
              return <TextField fullWidth {...params} sx={inputStyle} />;
            }}
          />
        </Box>
      </LocalizationProvider>
    </div>
  );
}
