import React, { useState } from "react";
import logo from "../images/logo.png";
import { HeaderText } from "../components/HeaderText";
import LoginComponent from "./LoginComponent";
import { connect } from "react-redux";
import LoginSuccessComponent from "./LoginSuccessComponent";

export default connect((state) => state)(function Header(props) {
  //props.dispatch(setLogin());
  const login = JSON.parse(sessionStorage.getItem("loginMember"));
  return (
    <>
      <img src={logo} alt="학원이미지" width="200" height="60" />
      <HeaderText title="Conference Reservation"></HeaderText>
      <div id="login-container">
        {login != null && login["memberId"] != null ? (
          <LoginSuccessComponent setCaldendar={props.setCaldendar} />
        ) : (
          <LoginComponent />
        )}
      </div>
    </>
  );
});
