import React from "react";
import logo from "../images/logo.png";

export default function LoadingPage() {
  const style = {
    position: "absolute",
    top: "20%",
    left: "45%",
    width: "18%",
    height: "18%",
  };
  return (
    <div id="loadingImg">
      <img alt="로딩페이지" src={logo} style={style} />
    </div>
  );
}
