import React, { useState } from "react";
import Modal from "@mui/material/Modal";
import { Typography } from "@mui/material";
import ButtonGourp from "@mui/material/ButtonGroup";
import Button from "@mui/material/Button";
import AddTaskOutlinedIcon from "@mui/icons-material/AddTaskOutlined";
import CancelOutlinedIcon from "@mui/icons-material/CancelOutlined";
import {
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from "@mui/material";
import Box from "@mui/material/Box";
import { HeaderText } from "./HeaderText";
import { connect } from "react-redux";
import { serverinfo } from "./ServerInfo";
import { setAllConference } from "../reduxconfig/actions/LoginAction";
import AddIcon from "@mui/icons-material/Add";
import FloorModal from "./FloorModal";

const ConferrenceModal = (props) => {
  const { confModalFlag, setConfModal } = props;
  const [floor, setFloor] = useState(11);
  const [confereceContent, setConferenceContent] = useState({});
  const [floorModal, setFloorModal] = useState(false);

  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    bgcolor: "background.paper",
    border: "2px solid #000",
    boxShadow: 24,
    p: 4,
  };
  const fn_addConference = (e) => {
    console.log(confereceContent);
    fetch(`${serverinfo}/conference`, {
      method: "post",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(confereceContent),
    })
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        if (data["confContentNo"] != 0) {
          fetch(`${serverinfo}/conferences/all`)
            .then((response) => {
              return response.json();
            })
            .then((conferece) => {
              props.dispatch(setAllConference(conferece));
              setConfModal(false);
            });
        } else {
          alert("입력실패 다시 입력하세요!");
        }
      });
  };

  const fn_closeModal = (e) => {
    setConfModal(false);
  };

  const selectChange = (e) => {
    setFloor(e.target.value);
    props.curConf.conferenceInfo.forEach((v) => {
      if (v["confNo"] == e.target.value) {
        setConferenceContent((prev) => ({ ...prev, confNoRef: v["confNo"] }));
      }
    });
  };
  const inputData = (title) => {
    return (e) => {
      setConferenceContent((prev) => ({ ...prev, [title]: e.target.value }));
    };
  };
  const fn_openFloor = () => {
    setFloorModal(true);
  };
  return (
    <>
      <Modal
        open={confModalFlag}
        onClose={fn_closeModal}
        aria-labelledby="modal-title"
        aria-describedby="modla-content"
      >
        <Box sx={style}>
          <Typography id="modal-title">
            <HeaderText title="회의실 등록하기" />
          </Typography>
          <Typography id="modal-content">
            <Box>
              <Box display={"flex"}>
                <FormControl
                  fullWidth
                  sx={{ marginBottom: "5%", display: "flex" }}
                >
                  <InputLabel id="demo-simple-select-label">층</InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={floor}
                    label="Age"
                    onChange={selectChange}
                  >
                    {props.curConf.conferenceInfo.map((v) => {
                      return (
                        <MenuItem
                          key={`menu_${v["confNo"]}`}
                          value={v["confNo"]}
                        >
                          {v["floor"]}층
                        </MenuItem>
                      );
                    })}
                  </Select>
                </FormControl>
                <Button
                  color="primary"
                  startIcon={<AddIcon size="large" />}
                  onClick={fn_openFloor}
                >
                  add
                </Button>
              </Box>
              <TextField
                sx={{ marginBottom: "5%" }}
                fullWidth
                label="회의실이름"
                variant="outlined"
                color="secondary"
                onChange={inputData("confContentName")}
              />
              <TextField
                sx={{ marginBottom: "5%" }}
                fullWidth
                label="제안인원수"
                variant="outlined"
                color="secondary"
                onChange={inputData("limitCount")}
              />
            </Box>
            <Box sx={{ display: "flex", justifyContent: "right" }}>
              <ButtonGourp>
                <Button
                  color="success"
                  startIcon={<AddTaskOutlinedIcon />}
                  onClick={fn_addConference}
                >
                  회의실등록하기
                </Button>
                <Button
                  color="error"
                  startIcon={<CancelOutlinedIcon />}
                  onClick={fn_closeModal}
                >
                  닫기
                </Button>
              </ButtonGourp>
            </Box>
          </Typography>
        </Box>
      </Modal>
      <FloorModal floorModal={floorModal} setFloorModal={setFloorModal} />
    </>
  );
};
export default connect((state) => state)(ConferrenceModal);
