import React from "react";
import Modal from "@mui/material/Modal";
import { Typography } from "@mui/material";
import ButtonGourp from "@mui/material/ButtonGroup";
import Button from "@mui/material/Button";
import CheckCircleOutlineIcon from "@mui/icons-material/CheckCircleOutline";
import CancelOutlinedIcon from "@mui/icons-material/CancelOutlined";
import { HeaderText } from "./HeaderText";
import Box from "@mui/material/Box";
import { serverinfo } from "./ServerInfo";

export default function ReserveViewModal({ eventController, modalController }) {
  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    bgcolor: "background.paper",
    border: "2px solid #000",
    boxShadow: 24,
    p: 4,
  };

  const { eventViewData } = eventController;
  const { reserveModalFlag, setReserveFlag } = modalController;

  const fn_resetRevserve = () => {
    const loginMember = JSON.parse(sessionStorage.getItem("loginMember"));
    if (
      loginMember != null &&
      (loginMember["memberId"] == eventViewData.event._def.publicId ||
        loginMember["memberLevel"] == 1)
    ) {
      if (window.confirm("예약을 취소하시겠습니까?")) {
        const eventContent = eventViewData["event"];
        const revNo = eventContent["_def"]["extendedProps"]["reservNo"];
        eventViewData.event.remove();
        fetch(`${serverinfo}/event/${revNo}`, {
          method: "DELETE",
        });
      }
    } else {
      alert("예약한사람만 취소할 수 있습니다!");
    }
    setReserveFlag(false);
  };

  return (
    <div>
      <Modal
        open={reserveModalFlag}
        onClose={() => {
          setReserveFlag(false);
        }}
        aria-labelledby="modal-title"
        aria-describedby="modla-content"
      >
        <Box sx={style}>
          <Typography id="modal-title">
            <HeaderText title="예약확인" />
          </Typography>
          <Typography id="modal-content">
            {typeof eventViewData.event != "undefined" ? (
              <>
                <h3>예약자 : {eventViewData.event._def["publicId"]}</h3>
                <h3>내용 : {eventViewData.el.innerText} </h3>
                <h3>
                  수강반 :&nbsp;
                  {eventViewData["event"]["_def"]["extendedProps"]["className"]}
                </h3>
              </>
            ) : (
              ""
            )}
            <Box sx={{ display: "flex", justifyContent: "right" }}>
              <ButtonGourp>
                <Button
                  color="error"
                  startIcon={<CancelOutlinedIcon />}
                  onClick={fn_resetRevserve}
                >
                  취소하기
                </Button>
                <Button
                  color="primary"
                  startIcon={<CheckCircleOutlineIcon />}
                  onClick={() => {
                    setReserveFlag(false);
                  }}
                >
                  닫기
                </Button>
              </ButtonGourp>
            </Box>
          </Typography>
        </Box>
      </Modal>
    </div>
  );
}
