import React from "react";
import Tab from "@mui/material/Tab";
import Box from "@mui/material/Box";
import { Consumer } from "../contextdata/ContextConfig";
import { connect } from "react-redux";
import { setCurConference } from "../reduxconfig/actions/LoginAction";
import { serverinfo } from "./ServerInfo";
import { setEvent } from "../reduxconfig/actions/EventAction";
const menuTap = function MenuTap(props) {
  const handleChange = (e) => {
    //alert("ttest");
    const targetChildren = e.target.parentElement.children;
    Array.prototype.forEach.call(targetChildren, (v) => {
      v.style.background = "inherit";
      v.style.borderBottom = "";
      v.style.borderRadius = "10px";
    });
    e.target.style.backgroundColor = "lightgreen";
    props.dispatch(setCurConference(e.target.id));
    fetch(`${serverinfo}/event/${e.target.id}`)
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        const eventObj = props["events"]["calEvent"];
        //초기화하기
        const allEvents = eventObj.getEvents();
        if (allEvents.length > 0) {
          allEvents.forEach((v) => {
            v.remove();
          });
        }

        //DB이벤트추가하기
        data.forEach((v) => {
          const e = {
            title: v["title"],
            start: new Date(v["start"]),
            end: new Date(v["end"]),
            id: v["revervedMember"]["memberId"],
            extendedProps: {
              reservNo: v["reservationno"],
              className: v["revervedMember"]["className"],
            },
          };
          eventObj.addEvent(e);
        });
      });
  };

  return (
    <Box sx={{ width: "100%", marginBottom: "2%" }}>
      <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
        <Consumer>
          {({ conference, calendarcheck }) => {
            return conference.map((title, i) => {
              if (Number(title["confNo"]) === Number(calendarcheck["index"])) {
                return title["details"].map((v, i) => {
                  return (
                    <Tab
                      id={v["confContentNo"]}
                      label={v["confContentName"]}
                      key={v["confContentNo"] + v["confContentName"] + i}
                      sx={{
                        fontSize: "26px",
                        fontWeight: "bolder",
                        color: "#FF9436",
                      }}
                      onClick={handleChange}
                    />
                  );
                });
              }
            });
          }}
        </Consumer>
      </Box>
    </Box>
  );
};

export default connect((state) => state)(menuTap);
