import React, { useState } from "react";
import { Consumer } from "../contextdata/ContextConfig";
import Button from "@mui/material/Button";
import Box from "@mui/material/Box";
import CalendarContainer from "./CalendarContainer";
import DirectionsRunIcon from "@mui/icons-material/DirectionsRun";
import { connect } from "react-redux";
import { setOpenModal } from "../reduxconfig/actions/LoginAction";

const sideMenu = function SideMenu(props) {
  const moveStyle = {
    transform: "translateX(20px)",
  };

  const fn_setContainer = (e, calendarcheck, setCaldendar) => {
    const loginCheck = JSON.parse(sessionStorage.getItem("loginMember"));
    if (loginCheck == null) {
      alert("로그인 후 이용이 가능합니다.");
      props.dispatch(setOpenModal());
    } else {
      const btns = document.querySelectorAll("aside button");
      btns.forEach((v, i) => {
        v.style.backgroundColor = "inherit";
        v.style.color = "#1976d2";
      });

      e.target.style.backgroundColor = "lightgreen";

      setCaldendar({
        index: e.target.id,
      });
    }
  };
  const moveImg = (e) => {
    const width = e.target.offsetWidth;
    e.target.previousElementSibling.previousElementSibling.style.transform = `translateX(${
      width - 20
    }px)`;
    e.target.previousElementSibling.previousElementSibling.style.transition =
      "transform 0.5s";
  };
  const removeImg = (e) => {
    e.target.previousElementSibling.previousElementSibling.style.transform = "";
  };
  return (
    <Consumer>
      {({ conference, setCaldendar }) => (
        <>
          {conference.map((v, i) => {
            return (
              <div key={`con_${v + i}`}>
                <Box key={`b_${v + i}`} sx={{ marginBottom: "15%" }}>
                  <DirectionsRunIcon
                    key={`i_${v + i}`}
                    color="primary"
                    sx={{ fontSize: "40px" }}
                  />
                  <br />
                  <Button
                    id={v["confNo"]}
                    key={v["confNo"] + v["floor"] + i}
                    variant="outlined"
                    size="large"
                    onClick={(e) => {
                      fn_setContainer(e, v["confNo"], setCaldendar);
                    }}
                    color="primary"
                    sx={{
                      width: "80%",
                      fontSize: "120%",
                      fontWeight: "bolder",
                    }}
                    onMouseEnter={moveImg}
                    onMouseLeave={removeImg}
                  >
                    {v["floor"]}층 강의장
                  </Button>
                </Box>
              </div>
            );
          })}
        </>
      )}
    </Consumer>
  );
};

export default connect((state) => state)(sideMenu);
