import React, { useState } from "react";
import Modal from "@mui/material/Modal";
import { Typography, InputAdornment } from "@mui/material";
import Box from "@mui/material/Box";
import { HeaderText } from "./HeaderText";
import ButtonGourp from "@mui/material/ButtonGroup";
import Button from "@mui/material/Button";
import { TextField } from "@mui/material";
import AccountCircleTwoToneIcon from "@mui/icons-material/AccountCircleTwoTone";
import CancelTwoToneIcon from "@mui/icons-material/CancelTwoTone";
import AccountCircle from "@mui/icons-material/AccountCircle";
import VpnKeyIcon from "@mui/icons-material/VpnKey";
import { serverinfo } from "../components/ServerInfo";
import { connect } from "react-redux";
import { setCloseModal, setLogin } from "../reduxconfig/actions/LoginAction";

export default connect((state) => state)(function LoginModal(props) {
  const { loginModal } = props.modalFlag;
  const [loginInfo, setLoginInfo] = useState({ memberId: "", password: "" });
  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    bgcolor: "background.paper",
    border: "2px solid #000",
    boxShadow: 24,
    p: 4,
  };

  const fn_handleClose = () => {
    props.dispatch(setCloseModal());
  };
  const fn_handleLogin = () => {
    fetch(
      // `${serverinfo}/reservation/events/=${loginInfo["userId"]}&passoword=${loginInfo["password"]}`
      `${serverinfo}/login`,
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(loginInfo),
      }
    )
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        if (data["memberId"] != null) {
          sessionStorage.setItem("loginMember", JSON.stringify(data));
          props.dispatch(setCloseModal());
          props.dispatch(setLogin());
        } else {
          alert("로그인실패 다시시도하세요!");
        }
      });
  };
  const fn_handleChange = (title) => {
    return (e) => {
      setLoginInfo((prev) => ({
        ...prev,
        [title]: e.target.value,
      }));
    };
  };
  return (
    <>
      <Modal
        open={loginModal}
        onClose={fn_handleClose}
        aria-labelledby="modal-title"
        aria-describedby="modla-content"
      >
        <Box sx={style}>
          <Box
            id="modal-title"
            display={"flex"}
            flexDirection={"column"}
            justifyContent={"space-between"}
            alignItems={"center"}
            marginBottom={"5%"}
            sx={{
              background: "linear-gradient(to bottom, lightpink, white)",
              borderRadius: "20px",
            }}
          >
            <HeaderText title="로그인" color="beige" />
          </Box>
          <Typography id="modal-content">
            <Box
              height="130px"
              display={"flex"}
              flexDirection={"column"}
              justifyContent={"space-between"}
              marginBottom={"5%"}
            >
              <TextField
                fullWidth
                label="로그인"
                variant="outlined"
                color="secondary"
                onBlur={fn_handleChange("memberId")}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <AccountCircle />
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                fullWidth
                label="패스워드"
                variant="outlined"
                color="secondary"
                onBlur={fn_handleChange("password")}
                type={"password"}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <VpnKeyIcon />
                    </InputAdornment>
                  ),
                }}
              />
            </Box>
            <Box sx={{ display: "flex", justifyContent: "right" }}>
              <ButtonGourp>
                <Button
                  color="success"
                  startIcon={<AccountCircleTwoToneIcon />}
                  onClick={fn_handleLogin}
                >
                  로그인
                </Button>
                <Button
                  color="error"
                  startIcon={<CancelTwoToneIcon />}
                  onClick={fn_handleClose}
                >
                  취소
                </Button>
              </ButtonGourp>
            </Box>
          </Typography>
        </Box>
      </Modal>
    </>
  );
});
