import React, { useState } from "react";
import LoginTwoTone from "@mui/icons-material/LoginTwoTone";
import PersonAddAlt1RoundedIcon from "@mui/icons-material/PersonAddAlt1Rounded";
import { Grid } from "@mui/material";
import LoginModal from "./LoginModal";
import { connect } from "react-redux";
import { setOpenModal } from "../reduxconfig/actions/LoginAction";

export default connect((state) => state)(function LoginComponent(props) {
  const [loginFlag, setLoginFlag] = useState({ login: true, logout: true });
  //const [loginModal, setLoadinModal] = useState(false);
  const { logginModal } = props.modalFlag;

  const fn_login = (e) => {
    props.dispatch(setOpenModal());
  };
  const fn_enrollMember = (e) => {
    alert("회원가입");
  };

  const fn_loginHover = (title) => {
    return (e) => {
      setLoginFlag((prev) => ({
        ...prev,
        [title]: !prev[title],
      }));
      e.stopPropagation();
    };
  };
  return (
    <>
      <Grid container>
        <Grid
          display={"flex"}
          alignItems={"center"}
          alignContent={"center"}
          item
          md={6}
          onMouseEnter={fn_loginHover("login")}
          onMouseLeave={fn_loginHover("login")}
        >
          {loginFlag["login"] ? (
            <LoginTwoTone fontSize="large" onClick={fn_login} />
          ) : (
            <h5 onClick={fn_login}>로그인</h5>
          )}
        </Grid>
        <Grid
          display={"flex"}
          alignItems={"center"}
          alignContent={"center"}
          textAlign={"center"}
          item
          md={6}
          onMouseEnter={fn_loginHover("logout")}
          onMouseLeave={fn_loginHover("logout")}
        >
          {loginFlag["logout"] ? (
            <PersonAddAlt1RoundedIcon
              fontSize="large"
              onClick={fn_enrollMember}
            />
          ) : (
            <h5 onClick={fn_enrollMember}>회원가입</h5>
          )}
        </Grid>
      </Grid>

      <LoginModal />
    </>
  );
});
