import { SET_LOGIN, SET_LOGOUT,SET_ADMIN } from "../actions/LoginAction";

export default (state = {}, action) => {
  const { type, payload } = action;
  switch (type) {
    case SET_LOGIN: {
      return { ...state, loginCehck: payload };
    }
    case SET_LOGOUT: {
      {
        return { ...state, loginCehck: payload };
      }
    }
    case SET_ADMIN:{
      {
        return {...state,adminCheck:payload};
      }
    }
    default : return state;
  }
}
