import React, { useState } from "react";
import Modal from "@mui/material/Modal";
import { Typography } from "@mui/material";
import ButtonGourp from "@mui/material/ButtonGroup";
import Button from "@mui/material/Button";
import AddTaskOutlinedIcon from "@mui/icons-material/AddTaskOutlined";
import CancelOutlinedIcon from "@mui/icons-material/CancelOutlined";
import ConferenceInput from "./ConferenceInput";
import { HeaderText } from "./HeaderText";
import Box from "@mui/material/Box";

export default function EnrollModal({ eventscontroller, modalcontroller }) {
  const { events, setEvents, fn_addEvent } = eventscontroller;
  const { enrollModalFlag, setEnrollModalFlag } = modalcontroller;
  const [sendFlag, setSendFlag] = useState(false);
  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    bgcolor: "background.paper",
    border: "2px solid #000",
    boxShadow: 24,
    p: 4,
  };

  const fn_handleClose = () => {
    setEnrollModalFlag(false);
  };

  return (
    <div>
      <Modal
        open={enrollModalFlag}
        onClose={fn_handleClose}
        aria-labelledby="modal-title"
        aria-describedby="modla-content"
      >
        <Box sx={style}>
          <Typography id="modal-title">
            <HeaderText title="예약신청" />
          </Typography>
          <Typography id="modal-content">
            <ConferenceInput
              event={{
                events: events,
                setEvents: setEvents,
                sendFlag: sendFlag,
                setSendFlag: setSendFlag,
              }}
            />
            <Box sx={{ display: "flex", justifyContent: "right" }}>
              <ButtonGourp>
                <Button
                  color="success"
                  startIcon={<AddTaskOutlinedIcon />}
                  onClick={fn_addEvent}
                  disabled={sendFlag ? true : false}
                >
                  예약신청하기
                </Button>
                <Button
                  color="error"
                  startIcon={<CancelOutlinedIcon />}
                  onClick={fn_handleClose}
                >
                  닫기
                </Button>
              </ButtonGourp>
            </Box>
          </Typography>
        </Box>
      </Modal>
    </div>
  );
}
