
import './App.css';
import SideMenu from './components/SideMenu';
import {useState,useEffect} from 'react';
import { Provider } from './contextdata/ContextConfig';
import Header from './components/Header';
import CalendarContainer from './components/CalendarContainer';
import {conferenceData} from './contextdata/ConferenceData';//샘플데이터
import { serverinfo } from './components/ServerInfo';
import LoadingPage from './components/LoadingPage';
import MenuTap from './components/MenuTap';
import { HeaderText } from './components/HeaderText';
import { connect } from "react-redux";
//store값을 가져오기 위해 연결하기
import {setAllConference} from './reduxconfig/actions/LoginAction';
function App(props) {
  const [loading,setLoading]=useState(true);
  const [calendarcheck,setCaldendar]=useState();
  const loginMember=JSON.parse(sessionStorage.getItem("loginMember"));
  useEffect(()=>{
    //라이프라이클을 이용해서  componentDidMount component로드시 
    //회의실에 대한 정보를 불러오는 기능
    //메뉴관련 내용 DB에서 받아오기
    fetch(`${serverinfo}/conferences/all`)
    .then(response=>{
      return response.json()
    }).then(data=>{
      props.dispatch(setAllConference(data));
      //setCaldendar({index:conference[0]["confNo"],container:CalendarContainer});
      //회의실별 예약내용을 확인하기 위한 데이터 저장
      setLoading(false);//로딩페이지 구성
  })
  },[]);
  return (
      <Provider value={{"conference":props.curConf.conferenceInfo, calendarcheck:calendarcheck,setCaldendar:setCaldendar}}>
        <div className="App">
          {loading?<LoadingPage/>:
            <>
            <header className="App-header">
               <Header setCaldendar={setCaldendar}/>:
            </header>
            <section>
              <aside>
                <SideMenu />
              </aside>
              <article>
                {calendarcheck&&calendarcheck['index']?
                <>
                  <MenuTap />
                  <CalendarContainer></CalendarContainer>
                </>:<HeaderText title="예약을 원하는 층을 선택하세요!"></HeaderText>
                }
              </article>
            </section>
            </>
          }
        </div>
       </Provider>
  );
}

export default connect(state=>state)(App);
